.container {
  display: table;
  width: 100%;
  padding-top: 20px;
}
.column {
  display: table-cell;
}
.columnLeft {
  display: table-cell;
  padding-left: 16px;
  text-align: left;
  line-height: 1.5;
  display: inline-block;
  vertical-align: middle;
}
.columnRight {
  display: table-cell;
  padding-right: 16px;
  text-align: left;
  line-height: 1.5;
  display: inline-block;
  vertical-align: middle;
}

.head1 {
  font-size: 2.5em;
  font-weight: 750;
  padding-bottom: 20px;
}

